import { createContext, useContext, useState, useEffect } from "react";
import type { ReactNode } from "react";

export type Theme = "light" | "dark";

interface ThemeContextType {
  theme: Theme;
  setTheme: (theme: Theme) => void;
  toggleTheme: () => void;
}

const ThemeContext = createContext<ThemeContextType | undefined>(undefined);

interface ThemeProviderProps {
  children: ReactNode;
}

export function ThemeProvider({ children }: ThemeProviderProps) {
  const [theme, setThemeState] = useState<Theme>(() => {
    // 1. Check localStorage first
    const stored = localStorage.getItem("cockpit-theme") as Theme | null;
    if (stored === "light" || stored === "dark") {
      return stored;
    }

    // 2. Fall back to system preference
    if (window.matchMedia("(prefers-color-scheme: dark)").matches) {
      return "dark";
    }

    // 3. Default to light
    return "light";
  });

  // Apply theme to cockpit root element instead of document
  useEffect(() => {
    // Try to find the cockpit root element (for injection mode)
    const cockpitRoot = document.querySelector(".cockpit-headup") as HTMLElement;
    
    if (cockpitRoot) {
      // Inject mode: apply theme to cockpit container
      cockpitRoot.setAttribute("data-theme", theme);
    } else {
      // Standalone mode: apply to document as fallback
      document.documentElement.setAttribute("data-theme", theme);
    }
    
    localStorage.setItem("cockpit-theme", theme);
  }, [theme]);

  // Listen for system theme changes
  useEffect(() => {
    const mediaQuery = window.matchMedia("(prefers-color-scheme: dark)");
    
    const handleChange = (e: MediaQueryListEvent) => {
      // Only auto-switch if user hasn't set a preference
      const storedTheme = localStorage.getItem("cockpit-theme");
      if (!storedTheme) {
        setThemeState(e.matches ? "dark" : "light");
      }
    };

    mediaQuery.addEventListener("change", handleChange);
    return () => mediaQuery.removeEventListener("change", handleChange);
  }, []);

  const setTheme = (newTheme: Theme) => {
    setThemeState(newTheme);
  };

  const toggleTheme = () => {
    setThemeState(prev => prev === "light" ? "dark" : "light");
  };

  return (
    <ThemeContext.Provider value={{ theme, setTheme, toggleTheme }}>
      {children}
    </ThemeContext.Provider>
  );
}

export function useTheme() {
  const context = useContext(ThemeContext);
  if (context === undefined) {
    throw new Error("useTheme must be used within a ThemeProvider");
  }
  return context;
}
