/**
 * Cockpit Headup - Configuration
 */

// Detect if running in injected mode (on a project domain) or standalone
// Check if we're on a known project domain
const isProjectDomain = window.location.hostname.includes('robinswood.io') ||
                        window.location.hostname.includes('localhost');

// When injected, use the current host; otherwise use localhost for dev
const isInjected = isProjectDomain && document.getElementById('cockpit-headup-root') !== null;

console.log('[Cockpit Config] isInjected:', isInjected);
console.log('[Cockpit Config] hostname:', window.location.hostname);

// Pilote API URL
export const PILOTE_URL = isInjected
  ? `${window.location.protocol}//${window.location.host}/api/chatbot`
  : 'http://localhost:3100';

// Pilote WebSocket URL
export const PILOTE_WS_URL = isInjected
  ? `${window.location.protocol}//${window.location.host}`
  : 'http://localhost:3100';

// Pilote Socket.IO path
export const PILOTE_SOCKET_PATH = isInjected
  ? '/api/chatbot/socket.io'
  : '/socket.io';

console.log('[Cockpit Config] PILOTE_WS_URL:', PILOTE_WS_URL);
console.log('[Cockpit Config] PILOTE_SOCKET_PATH:', PILOTE_SOCKET_PATH);
