import './CockpitOverlay.css';
import { ModernChatInterface } from './ModernChatInterface';

interface CockpitOverlayProps {
  project: string;
  isInjected: boolean;
}

export function CockpitOverlay({ project, isInjected }: CockpitOverlayProps) {

  if (!project) {
    // In standalone mode, show project selector message
    if (!isInjected) {
      return (
        <div className="cockpit-no-project">
          <p> Slectionnez un projet pour commencer</p>
          <p className="hint">Le slecteur vous redirigera vers l'URL du projet</p>
        </div>
      );
    }

    // In injected mode but no project detected, show error
    return (
      <div className="cockpit-no-project">
        <p> Impossible de dtecter le projet actuel</p>
        <p className="hint">Assurez-vous d'tre sur l'URL d'un projet (ex: jlm-app.robinswood.io)</p>
      </div>
    );
  }

  // Use ModernChatInterface which handles its own FAB and window
  return <ModernChatInterface project={project} />;
}
