import { useState, useEffect, useCallback } from 'react';
import { io, Socket } from 'socket.io-client';
import { PILOTE_WS_URL, PILOTE_SOCKET_PATH } from '../config';

interface AgentProgress {
  step: string;
  message: string;
  tool?: string;
  args?: any;
}

interface AgentResult {
  success: boolean;
  message: string;
  changes: any[];
  iterations: number;
}

export function usePiloteAgent() {
  const [socket, setSocket] = useState<Socket | null>(null);
  const [connected, setConnected] = useState(false);
  const [executing, setExecuting] = useState(false);
  const [progress, setProgress] = useState<AgentProgress | null>(null);

  useEffect(() => {
    const newSocket = io(PILOTE_WS_URL, {
      path: PILOTE_SOCKET_PATH,
      transports: ['websocket', 'polling']
    });

    newSocket.on('connect', () => {
      console.log(' Connected to Pilote');
      setConnected(true);
    });

    newSocket.on('disconnect', () => {
      console.log(' Disconnected from Pilote');
      setConnected(false);
    });

    newSocket.on('agent:progress', (data: AgentProgress) => {
      setProgress(data);
    });

    newSocket.on('agent:result', (_data: { result: AgentResult }) => {
      setExecuting(false);
      setProgress(null);
    });

    newSocket.on('agent:error', (error: { error: string }) => {
      console.error('Agent error:', error);
      setExecuting(false);
      setProgress(null);
    });

    setSocket(newSocket);

    return () => {
      newSocket.close();
    };
  }, []);

  const execute = useCallback((query: string, project: string): Promise<AgentResult> => {
    return new Promise((resolve, reject) => {
      if (!socket || !connected) {
        reject(new Error('Not connected to Pilote'));
        return;
      }

      setExecuting(true);
      setProgress(null);

      // Listen for result
      const resultListener = (data: { result: AgentResult }) => {
        setExecuting(false);
        socket.off('agent:result', resultListener);
        socket.off('agent:error', errorListener);
        resolve(data.result);
      };

      const errorListener = (error: { error: string }) => {
        setExecuting(false);
        socket.off('agent:result', resultListener);
        socket.off('agent:error', errorListener);
        reject(new Error(error.error));
      };

      socket.once('agent:result', resultListener);
      socket.once('agent:error', errorListener);

      // Get userId from localStorage (with SSR safety check)
      const userId = typeof window !== 'undefined' && window.localStorage 
        ? window.localStorage.getItem("cockpit-user-id") || "default-user" 
        : "default-user";

      // Send execute request with userId
      socket.emit('agent:execute', {
        query,
        project,
        userId,
        options: {
          autoCommit: true,
          runTests: false
        }
      });
    });
  }, [socket, connected]);

  return {
    connected,
    executing,
    progress,
    execute
  };
}
