/**
 * Injectable version of Cockpit HeadsUp
 * Can be loaded in any application to add the cockpit overlay
 */
import React from 'react';
import ReactDOM from 'react-dom/client';
import { CockpitOverlay } from './components/CockpitOverlay';
import { ThemeProvider } from './contexts/ThemeContext';
import './App.css';

export interface CockpitHeadsUpConfig {
  project?: string;
  containerId?: string;
  autoDetect?: boolean;
}

class CockpitHeadsUp {
  private root: ReactDOM.Root | null = null;
  private container: HTMLElement | null = null;
  private config: CockpitHeadsUpConfig;

  constructor(config: CockpitHeadsUpConfig = {}) {
    this.config = {
      containerId: 'cockpit-headup-root',
      autoDetect: true,
      ...config,
    };
  }

  /**
   * Detect project from URL hostname
   */
  private detectProject(): string | null {
    if (!this.config.autoDetect) {
      return this.config.project || null;
    }

    try {
      const hostname = window.location.hostname;
      const match = hostname.match(/^([^.]+)\./);
      if (match) {
        const project = match[1];
        const projectMap: Record<string, string> = {
          'paperbridge': 'paperbridge',
          'cjd80': 'cjd80',
          'game-plug': 'game-plug',
          'saxium': 'saxium',
          'jlm-app': 'jlm-app',
          'work': 'work-hub',
        };
        return projectMap[project] || project;
      }
    } catch (e) {
      console.warn('[Cockpit HeadsUp] Cannot detect project from URL', e);
    }

    return this.config.project || null;
  }

  /**
   * Initialize and mount the cockpit overlay
   */
  mount(): void {
    if (this.root) {
      console.warn('[Cockpit HeadsUp] Already mounted');
      return;
    }

    // Create container if it doesn't exist
    this.container = document.getElementById(this.config.containerId!) as HTMLElement;
    if (!this.container) {
      this.container = document.createElement('div');
      this.container.id = this.config.containerId!;
      this.container.style.position = 'fixed';
      this.container.style.top = '0';
      this.container.style.left = '0';
      this.container.style.width = '100%';
      this.container.style.height = '100%';
      this.container.style.pointerEvents = 'none';
      this.container.style.zIndex = '999999';
      document.body.appendChild(this.container);
    }

    // Detect project
    const project = this.detectProject();
    console.log('[Cockpit HeadsUp] Mounting with project:', project);

    // Render React app
    this.root = ReactDOM.createRoot(this.container);
    this.root.render(
      <React.StrictMode>
        <ThemeProvider>
          <CockpitOverlay project={project || ''} isInjected={true} />
        </ThemeProvider>
      </React.StrictMode>
    );
  }

  /**
   * Unmount and cleanup
   */
  unmount(): void {
    if (this.root) {
      this.root.unmount();
      this.root = null;
    }

    if (this.container && this.container.parentNode) {
      this.container.parentNode.removeChild(this.container);
      this.container = null;
    }

    console.log('[Cockpit HeadsUp] Unmounted');
  }

  /**
   * Update project
   */
  setProject(project: string): void {
    this.config.project = project;
    if (this.root) {
      this.unmount();
      this.mount();
    }
  }

  /**
   * Check if mounted
   */
  isMounted(): boolean {
    return this.root !== null;
  }
}

// Export for ES modules
export { CockpitHeadsUp };

// Auto-initialize if loaded via script tag and window.CockpitHeadsUpConfig is set
if (typeof window !== 'undefined') {
  (window as any).CockpitHeadsUp = CockpitHeadsUp;

  // Auto-mount if config is provided
  if ((window as any).CockpitHeadsUpConfig) {
    const instance = new CockpitHeadsUp((window as any).CockpitHeadsUpConfig);
    instance.mount();
    (window as any).cockpitHeadsUpInstance = instance;
  }
}

export default CockpitHeadsUp;
